import { useMutation, useQuery, useQueryClient } from "@tanstack/react-query";
import { CreateTaskDTO, Task, TaskStatus } from "@everlast/types";
import { createTask, listTasks, markTaskDone, reopenTask } from "@/lib/mock-api";

const DEFAULT_TENANT = "tenant-demo-1";

export function useTasks(tenantId: string = DEFAULT_TENANT) {
  return useQuery({
    queryKey: ["tasks", tenantId],
    queryFn: () => listTasks(tenantId),
  });
}

export function useCreateTask(tenantId: string = DEFAULT_TENANT) {
  const queryClient = useQueryClient();
  return useMutation({
    mutationFn: (payload: Omit<CreateTaskDTO, "tenantId">) =>
      createTask({ tenantId, ...payload }),
    onSuccess: (task: Task) => {
      queryClient.invalidateQueries({ queryKey: ["tasks", tenantId] });
      if (task.leadId) {
        queryClient.invalidateQueries({
          queryKey: ["tasks", tenantId, task.leadId],
        });
      }
    },
  });
}

export function useToggleTaskStatus(tenantId: string = DEFAULT_TENANT) {
  const queryClient = useQueryClient();
  return useMutation({
    mutationFn: ({ id, status }: { id: string; status: TaskStatus }) =>
      status === TaskStatus.DONE ? reopenTask(id, tenantId) : markTaskDone(id, tenantId),
    onSuccess: (task: Task) => {
      queryClient.invalidateQueries({ queryKey: ["tasks", tenantId] });
      if (task.leadId) {
        queryClient.invalidateQueries({
          queryKey: ["tasks", tenantId, task.leadId],
        });
      }
    },
  });
}

